/* eslint-disable max-len */
import React from 'react';
import {
  Alignment,
  Button,
  Classes,
  Navbar,
  NavbarDivider,
  NavbarGroup,
  NavbarHeading,
} from '@blueprintjs/core';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import { useAppSelector } from '../hooks/redux-hooks';
// import LogoutButton from './LogoutButton';

const UserIcon: React.FC = () => {
  const user = useAppSelector(state => state.user);

  return (
    <Link to={`/user/${user?.user_id}`}>
      <Button className={classNames(Classes.MINIMAL, 'header__user')}>
        <img src={user?.user_avatar} alt="" className="header__user__img" />
        {user?.user_email}
      </Button>
    </Link>
  );
};

export interface INavbarExampleState {
  alignRight: boolean;
}

export const Header: React.FC = () => {
  const user = useAppSelector(state => state.user);

  return (
    <header className="bp4-navbar header">
      <div className="container">
        <Navbar className="header">
          <NavbarGroup align={Alignment.LEFT}>
            <NavbarHeading>My App</NavbarHeading>
            <NavbarDivider />
            <Link to="/home">
              <Button className={Classes.MINIMAL} icon="home" text="Home" />
            </Link>
            <Link to="/company">
              <Button className={Classes.MINIMAL} icon="comparison" text="Companie" />
            </Link>
            <Link to="/user">
              <Button className={Classes.MINIMAL} icon="user" text="User" />
            </Link>
            <Link to="/about-us">
              <Button className={Classes.MINIMAL} text="AboutUs" />
            </Link>
            <Link to="/companies">
              <Button className={Classes.MINIMAL} icon="comparison" text="Companies" />
            </Link>
            <Link to="/users">
              <Button className={Classes.MINIMAL} icon="user" text="Users" />
            </Link>
            <Button className={Classes.MINIMAL} text="Open modal" />
          </NavbarGroup>
          <NavbarGroup align={Alignment.LEFT}>
            {!user ? (
              <>
                <Link to="/log-in">
                  <Button className={Classes.MINIMAL} icon="log-in" text="Log In" />
                </Link>
                <Link to="/register">
                  <Button className={Classes.MINIMAL} text="Register" />
                </Link>
              </>
            ) : (<UserIcon />)}
          </NavbarGroup>

        </Navbar>
      </div>
    </header>
  );
};
