import { User } from '../types/User';

type AddAction = {
  type: 'user/ADD',
  payload: User,
};

type RemoveAction = {
  type: 'user/REMOVE',
  payload: null,
};

type Action = RemoveAction | AddAction;

const remove = (): RemoveAction => ({
  type: 'user/REMOVE',
  payload: null,
});

const add = (user: User): AddAction => ({
  type: 'user/ADD',
  payload: user,
});

export const actions = { remove, add };

const userReducer = (user: User | null = null, action: Action): User | null => {
  switch (action.type) {
    case 'user/REMOVE':
      return null;

    case 'user/ADD':
      return action.payload;

    default:
      return user;
  }
};

export default userReducer;
