import React, { useMemo } from 'react';
import className from 'classnames';
import { User } from '../types/User';
import { Company } from '../types/Company';

type CellProps = {
  items: (string | number)[]
  columnsCount:number
};

const Cell:React.FC<CellProps> = ({ items, columnsCount }) => {
  return (
    <div
      className={className(
        'table__cell',
        `grid-template-columns-${columnsCount}`,
      )}
    >
      {items.map((value, i) => <div key={`${value + String(i)}`} className="table__cell__item">{value}</div>)}
    </div>
  );
};

type Props = {
  rows: (User | Company)[];
};

export const Table: React.FC<Props> = ({
  rows,
}) => {
  const columns = useMemo(() => {
    const items = Object.keys(rows[0]);

    return <Cell items={items} columnsCount={items.length} />;
  }, [rows]);
  const columnsCount = useMemo(() => Object.keys(rows[0]).length, [rows]);
  const cells = rows.map((item) => {
    const id = (('user_id' in item) && item.user_id)
    || (('company_id' in item) && item.company_id)
    || 0;

    return (
      <Cell
        key={id}
        items={Object.values(item)}
        columnsCount={columnsCount}
      />
    );
  });

  return (
    <div className={className('table')}>
      {columns}
      {cells}
    </div>

  );
};
