import {
  Button, Card, InputGroup, Intent,
} from '@blueprintjs/core';
import { Tooltip2 } from '@blueprintjs/popover2';
import React, {
  useState, useMemo, useEffect,
} from 'react';
import { Link } from 'react-router-dom';
import { regExps } from '../utils/RegExp';
import { AuthButton } from './AuthButton';
import Profile from './Profile';

type Props = {
  type: 'reg' | 'auth',
  buttonFn: (
    {
      loginValue,
      passValue,
    }: {
      loginValue: string,
      passValue: string,
    }
  ) => void,
};

export const AuthPanel: React.FC<Props> = ({ buttonFn, type }) => {
  // const loginInput = useRef<HTMLInputElement | null>(null);
  const [loginInput, setLoginInput] = useState('');
  // const passInput = useRef<HTMLInputElement | null>(null);
  const [passInput, setPassInput] = useState('');
  const [{ errorLoginInput, errorPassInput }, setErrorInputs] = useState({
    errorLoginInput: false,
    errorPassInput: false,
  });

  const [showPassword, setShowPassword] = useState(false);
  const [ButtonsState] = useState({
    active: false,
    alignText: undefined,
    disabled: false,
    fill: true,
    iconOnly: false,
    intent: Intent.NONE,
    loading: false,
    minimal: false,
    outlined: false,
    size: 'large',
    wiggling: false,
  });

  const lockButton = useMemo(() => {
    return (
      <Tooltip2 content={`${showPassword ? 'Hide' : 'Show'} Password`}>
        <Button
          icon={showPassword ? 'unlock' : 'lock'}
          intent={Intent.PRIMARY}
          minimal
          onClick={() => setShowPassword(prev => !prev)}
        />
      </Tooltip2>
    );
  }, [showPassword]);

  useEffect(() => {
    setErrorInputs(prev => ({ ...prev, errorLoginInput: false }));
  }, [loginInput]);

  useEffect(() => {
    setErrorInputs(prev => ({ ...prev, errorPassInput: false }));
  }, [passInput]);

  return (
    <Card>
      <InputGroup
        disabled={false}
        large
        placeholder="Enter your email..."
        readOnly={false}
        small={false}
        type="text"
        // inputRef={loginInput}
        value={loginInput}
        onChange={(e) => {
          setLoginInput(e.target.value);
        }}
        intent={errorLoginInput ? 'danger' : 'none'}
      />
      <InputGroup
        disabled={false}
        large
        placeholder="Enter your password..."
        readOnly={false}
        small={false}
        type={showPassword ? 'text' : 'password'}
        // inputRef={passInput}
        value={passInput}
        onChange={(e) => {
          setPassInput(e.target.value);
        }}
        rightElement={lockButton}
        intent={errorPassInput ? 'danger' : 'none'}
      />
      <Button
        text="Log in"
        large
        {...ButtonsState}
        onClick={() => {
          const loginValid = regExps.email.test(loginInput);
          const passValid = !!passInput;

          if (loginValid && passValid) {
            buttonFn({
              loginValue: loginInput,
              passValue: passInput,
            });
          } else {
            if (!loginValid) {
              setErrorInputs(prev => ({ ...prev, errorLoginInput: true }));
            }

            if (!passValid) {
              setErrorInputs(prev => ({ ...prev, errorPassInput: true }));
            }
          }
        }}
      />
      {type === 'auth' ? (
        <p>
          You don
          {'\''}
          t have an account,
          {' '}
          <Link to="/register">you can register</Link>
        </p>
      ) : (
        <p>
          You have an account,
          {' '}
          <Link to="/log-in">you can log in</Link>
        </p>
      )}

      <AuthButton />
      <Profile />
    </Card>
  );
};
