import React, { useEffect, useState, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import {
  Card,
} from '@blueprintjs/core';
import { User } from '../types/User';
import api from '../api/api';
import { Loader } from '../components/Loader';
import { useAppSelector } from '../hooks/redux-hooks';
import LogoutButton from '../components/LogoutButton';

type UserCardProps = {
  user_email: string,
  user_firstname: string,
  user_lastname: string,
  user_avatar: string,
  user_id: number,
};

const UserCard: React.FC<UserCardProps> = ({
  user_email, user_firstname, user_lastname, user_avatar, user_id,
}) => {
  const { userId = 0 } = useParams();

  return (
    <Card className="user-profile">
      <img src={user_avatar} alt="avatar" className="user-profile__avatar" />
      <div className="user-profile__email">{user_email}</div>
      <div className="user-profile__full-name">
        {user_firstname}
        {' '}
        {user_lastname}
      </div>
      {user_id === +userId && <LogoutButton />}
    </Card>
  );
};

export const UserProfile = () => {
  const userInRedux = useAppSelector(state => state.user);
  const { userId = 0 } = useParams();
  const [{ user, error }, setUser] = useState<{
    user: User | null,
    error: boolean,
  }>({ user: null, error: false });
  const [refreshCounter, setRefreshConter] = useState(0);

  useEffect(() => {
    async function getUserById() {
      try {
        const userFromApi = await api.getUser(+userId);

        setUser(prev => ({ ...prev, user: userFromApi }));
      } catch (e) {
        setUser(prev => ({ ...prev, error: true }));
      }
    }

    if (userInRedux?.user_id !== +userId) {
      getUserById();
    } else {
      setUser(prev => ({ ...prev, user: userInRedux }));
    }
  }, [userId, refreshCounter, userInRedux?.user_id, userInRedux]);

  const refreshReq = useCallback(
    () => {
      setUser(prev => ({ ...prev, error: false }));
      setRefreshConter(prev => prev + 1);
    },
    [],
  );

  return (
    <section>
      {user ? (
        <UserCard {...user} />
      ) : (
        <Loader
          error={error}
          errorMassage="Server error"
          refreshFn={refreshReq}
        />
      )}
    </section>
  );
};
