/* eslint-disable max-len */
import React, { useEffect } from 'react';
import { Routes, Route, Outlet } from 'react-router-dom';
import { useAuth0 } from '@auth0/auth0-react';
import { Header } from './components/Header';
import { useLocalStorage } from './hooks/useLockalStorage';
import { Loader } from './components/Loader';
import { useAppDispatch } from './hooks/redux-hooks';
import { actions as userActions } from './store/user';
import { User } from './types/User';
import { AboutUs } from './pages/AboutUs';
import { Authorizations } from './pages/Authorizations';
import { CompaniesList } from './pages/CompaniesList';
import { CompanyProfile } from './pages/CompanyProfile';
import { MainPage } from './pages/MainPage';
import { Register } from './pages/Register';
import { UserProfile } from './pages/UserProfile';
import { UsersList } from './pages/UsersList';
import { NoAuthRoute } from './routes/NoAuthRoute';
import { PrivateRoute } from './routes/PrivateRoute';

export const App: React.FC = () => {
  const dispatch = useAppDispatch();
  const auth0 = useAuth0();
  const { user, isAuthenticated, isLoading } = useAuth0();
  const [, setAccessToken] = useLocalStorage('accessToken', '');

  useEffect(() => {
    async function addAccessTokenInLocalStorage() {
      try {
        const accessToken = await auth0.getAccessTokenSilently();

        setAccessToken(accessToken);
      } catch (error) {
        setAccessToken('');
      }
    }

    addAccessTokenInLocalStorage();

    if (isAuthenticated && user !== undefined) {
      const userNormalize: User = {
        user_id: +user.sub!.split('|')[1],
        user_email: user.email!,
        user_firstname: user.given_name!,
        user_lastname: user.family_name!,
        user_avatar: user.picture!,
      };

      dispatch(userActions.add(userNormalize));
    }
  }, [auth0, dispatch, isAuthenticated, setAccessToken, user]);

  return !isLoading ? (
    <div className="app">
      <Header />
      <main>
        <div className="container">
          <Routes>
            <Route path="/">
              <Route element={<MainPage />}>
                <Route index element={<Outlet />} />
                <Route path="home" element={<Outlet />} />
              </Route>
              <Route path="about-us" element={<AboutUs />} />

              <Route element={<PrivateRoute />}>
                <Route path="company" element={<CompanyProfile />}>
                  <Route index element={<Outlet />} />
                  <Route path=":companie_id" element={<Outlet />} />
                </Route>
                <Route path="user" element={<UserProfile />}>
                  <Route index element={<Outlet />} />
                  <Route path=":userId" element={<Outlet />} />
                </Route>
                <Route path="companies" element={<CompaniesList />} />
                <Route path="users" element={<UsersList />} />
              </Route>

              <Route element={<NoAuthRoute />}>
                <Route path="log-in" element={<Authorizations />} />
                <Route path="register" element={<Register />} />
              </Route>
            </Route>
          </Routes>
        </div>
      </main>
    </div>
  ) : <Loader />;
};
