import React, { useEffect, useRef } from 'react';
import {
  Spinner,
  Button, Intent, Toaster,
} from '@blueprintjs/core';

type Props = {
  error?: boolean,
  errorMassage?: string,
  refreshFn?: () => void,
};

export const Loader: React.FC<Props> = ({
  error, errorMassage, refreshFn,
}) => {
  const toast = useRef<Toaster>(null);

  useEffect(() => {
    if (error) {
      toast.current?.show({
        message: errorMassage,
        intent: Intent.DANGER,
        icon: 'warning-sign',
      });
    }
  }, [error, errorMassage]);

  return (
    <div className="loader">
      {error ? (
        <>
          <Toaster ref={toast} />
          <Button
            className="button--reset"
            icon="reset"
            onClick={refreshFn}
          />
        </>
      ) : (<Spinner />)}

    </div>
  );
};
