import React, { useState, useEffect, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import api from '../api/api';
import { Company } from '../types/Company';
import { Loader } from '../components/Loader';

type CompanyCardProps = {
  company_name: string,
};

const CompanyCard: React.FC<CompanyCardProps> = ({
  company_name,
}) => {
  return (
    <div className="company-card">
      <div className="company-card__name">{company_name}</div>
    </div>
  );
};

export const CompanyProfile = () => {
  const { userId = 0 } = useParams();
  const [{ company, error }, setCompany] = useState<{
    company: Company | null,
    error: boolean,
  }>({ company: null, error: false });
  const [refreshCounter, setRefreshConter] = useState(0);

  useEffect(() => {
    async function getUserById() {
      try {
        const companyFromApi = await api.getCompany(+userId);

        setCompany(prev => ({ ...prev, company: companyFromApi }));
      } catch (e) {
        setCompany(prev => ({ ...prev, error: true }));
      }
    }

    getUserById();
  }, [userId, refreshCounter]);

  const refreshReq = useCallback(
    () => {
      setCompany(prev => ({ ...prev, error: false }));
      setRefreshConter(prev => prev + 1);
    },
    [],
  );

  return (
    <section>
      {company ? (
        <CompanyCard {...company} />
      ) : (
        <Loader
          error={error}
          errorMassage="Server error"
          refreshFn={refreshReq}
        />
      )}
    </section>
  );
};
